

































import { Component, Vue } from 'vue-property-decorator';
import {mapGetters} from 'vuex';

@Component({
    computed: {
        ...mapGetters({
        loggedIn: 'login/LOGGED_IN',
        }),
    },
})

export default class Login extends Vue {
    public message: string = '';
    public error: boolean = false;
    public user = {
        username: '',
        password: '',
    };

    public loginPerson(): void {
        this.$store.dispatch('login/LOG_IN_ACTION', this.user).then(
            () => setTimeout(() => this.loginCheckAndRedirect(), 2000),
        );
    }

    public created() {
        this.loginCheckAndRedirect();
    }

    private loginCheckAndRedirect(): void {
        const store = this.$store;
        if (store.getters['login/LOGGED_IN'] === true) {
            const path: string = store.state.login.role;
            this.$router.push('/' + path);
        }
    }
}
